import React, { Component } from "react";
import {
  Button,
  Col,
  Container,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Spinner,
} from "reactstrap";

//Import Breadcrumb
import { MDBDataTableV5 } from "mdbreact";
import { Link } from "react-router-dom";
import Breadcrumbs from "../../components/Common/Breadcrumb";

import HttpClient from "./../../utils/HttpClient";
class FormElements extends Component {
  constructor(props) {
    super(props);
    this.state = {
      breadcrumbItems: [
        { title: "Member Management", link: "#" },
        { title: "Manage User", link: "#" },
      ],
      customchk: true,
      toggleSwitch: true,
      // page states
      name: "",
      desc: "",
      duration: "",
      price: "",
      data: [],
      alert: false,
      message: "",
      type: "",
      result: [],
      edit_enable: false,
      edit_item_id: "",
      image_select: false,
      img_url: "",
      category_type: "",
      seletedFile: null,
      category_all: [],
      category_id: "",
      modal_standard: false,
      activeModal: null,
      deleteLoading: false,
    };
    this.tog_standard = this.tog_standard.bind(this);
  }

  tog_standard() {
    this.setState((prevState) => ({
      modal_standard: !prevState.modal_standard,
    }));
    this.removeBodyCss();
  }
  removeBodyCss() {
    document.body.classList.add("no_padding");
  }

  componentDidMount = async () => {
    let resultCat = await HttpClient.requestData("admin/menu-category", "GET");

    if (resultCat && resultCat.status) {
      this.setState({ category_all: resultCat.data });
    }
    this.fetchData();
  };

  handleChange = (event) => {
    // console.log(event.target.value);
    this.setState({ category_id: event.target.value });
  };

  deleteModal = (data, index) => {
    this.setState({ modal_standard: true, activeModal: data });
  };

  fetchData = async () => {
    let result = await HttpClient.requestData("admin/user", "GET");
    // console.log("result", result);
    if (result && result.status) {
      let data = [];
      let i = 1;
      this.setState({ result: result.data });
      this.state.result.forEach((element, index) => {
        let rows = {
          sl: i,
          fname: element.firstname,
          lname: element.lastname,
          email: element.email,
          gender: element.gender,
          ethncity: element.firstname,
          city: element.ethncity + " , " + element.zipcode,
          rgt_date: "",

          status: (
            <>
              {/* <button
                title="Delete"
                className="btn btn-danger mr-2"
                onClick={() => {
                  this.delete(element, index);
                }}
              >
                <i className="fa fa-trash" />
              </button> */}
              <button
                title={element.approve ? "Approve" : "Not Approve"}
                className={
                  element.approve ? "btn btn-success" : "btn btn-danger"
                }
                onClick={() => {
                  this.approve(element, index);
                }}
              >
                {element.approve ? "Approve" : "Disapprove"}
              </button>
            </>
          ),
          action: (
            <div style={{ display: "flex", flexDirection: "row" }}>
              <button
                title="Delete"
                className="btn btn-danger mr-2"
                onClick={() => {
                  this.deleteModal(element, index);
                }}
              >
                <i className="fa fa-trash" />
              </button>
              <Link
                to={"/user-profile/" + element._id}
                title="View"
                className="btn btn-info mr-2"
                onClick={() => {
                  // this.delete(element, index);
                }}
              >
                <i className="fa fa-eye" />
              </Link>
            </div>
          ),
        };
        i++;
        data.push(rows);
      });
      this.setState({
        data: data,
      });
    } else {
      this.setState({
        data: [],
      });
    }
  };

  delete = async () => {
    this.setState({ deleteLoading: true });
    // return false
    let result = await HttpClient.requestData(
      "admin/user/" + this.state.activeModal._id,
      "DELETE"
    );
    if (result && result.status) {
      let index = this.state.result.indexOf(this.state.activeModal);
      if (index > -1) {
        this.state.result.splice(index, 1);
        this.setState({
          alert: true,
          message: "Deleted Successfully",
          type: "success",
          deleteLoading: false,
          activeModal: null,
          modal_standard: false,
        });
        setTimeout(() => {
          this.setState({
            alert: false,
            message: "",
            type: "",
          });
        }, 3000);
        this.fetchData();
      }
    } else {
    }
  };

  edit = async (item, index) => {
    console.log("item.category", item);
    this.setState({
      edit_enable: true,
      name: item.name,
      edit_item_id: item._id,
      category_id: item.categoty,
    });
  };

  approve = async (item, index) => {
    let result = await HttpClient.requestData("admin/user/" + item._id, "PUT", {
      approve: !item.approve,
    });
    if (result && result.status) {
      this.setState({
        alert: true,
        message:
          item.approve == false
            ? "Approved Successfully."
            : "Not Approved Successfully.",
        type: "success",
      });
      this.fetchData();
    } else {
      this.setState({
        alert: true,
        message: "Server Error !!! Try Again.",
        type: "danger",
      });
    }

    setTimeout(() => {
      this.setState({
        alert: false,
        message: "",
        type: "",
      });
    }, 3000);
  };

  render() {
    const data = {
      columns: [
        {
          label: "Sl.",
          field: "sl",
          sort: "asc",
          width: 150,
        },
        {
          label: "First Name",
          field: "fname",
          sort: "asc",
          width: 270,
        },
        {
          label: "Last Name",
          field: "lname",
          sort: "asc",
          width: 270,
        },
        {
          label: "Email",
          field: "email",
          sort: "disabled",
          width: 270,
        },
        {
          label: "Gender",
          field: "gender",
          sort: "disabled",
          width: 270,
        },
        // {
        //   label: "City & Zipcode",
        //   field: "city",
        //   sort: "asc",
        //   width: 270,
        // },
        // {
        //   label: "Rgt. Date",
        //   field: "rgt_date",
        //   sort: "asc",
        //   width: 270,
        // },
        {
          label: "Status",
          field: "status",
          sort: "disabled",
          width: 100,
        },
        {
          label: "Actions",
          field: "action",
          sort: "disabled",
          width: 100,
        },
      ],
      rows: this.state.data,
    };
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid>
            {/* start of modal */}
            <Modal
              isOpen={this.state.modal_standard}
              toggle={this.tog_standard}
            >
              <ModalHeader
                toggle={() => this.setState({ modal_standard: false })}
              >
                Are you sure you want to delete{" "}
                {this.state.activeModal != null && this.state.activeModal.name}{" "}
                ?
              </ModalHeader>
              <ModalBody>
                <h5>
                  Name -{" "}
                  {this.state.activeModal != null &&
                    this.state.activeModal.firstname +
                      " " +
                      this.state.activeModal.lastname}
                </h5>
                <p>
                  If you want to delete click on Delete Now otherwize click on
                  close.
                </p>
              </ModalBody>
              <ModalFooter>
                <Button
                  type="button"
                  onClick={this.tog_standard}
                  color="light"
                  className="waves-effect"
                >
                  Close
                </Button>
                <Button
                  type="button"
                  color="danger"
                  className="waves-effect waves-light"
                  onClick={() => {
                    this.delete();
                  }}
                >
                  {this.state.deleteLoading ? (
                    <Spinner size="sm" color="secondary" />
                  ) : (
                    "Delete Now"
                  )}
                </Button>
              </ModalFooter>
            </Modal>
            {/* end of modal */}

            <Breadcrumbs
              title={this.state.breadcrumbItems[1].title}
              breadcrumbItems={this.state.breadcrumbItems}
            />

            <Row>
              <Col xl={12}>
                <div className="card p-3">
                  <MDBDataTableV5
                    responsive
                    bordered
                    data={data}
                    className={"table-data"}
                  />
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    );
  }
}

export default FormElements;
