import React, { Component } from "react";
import {
  Card,
  CardBody,
  Col,
  Row,
  Container,
  FormGroup,
  Label,
  Input,
  CustomInput,
  Button,
  UncontrolledAlert,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Spinner,
  Dropdown,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
} from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";
import { MDBDataTable, MDBDataTableV5 } from "mdbreact";
import { reactLocalStorage } from "reactjs-localstorage";
import moment from "moment";
import HttpClient from "./../../utils/HttpClient";
import { Link } from "react-router-dom";
class FormElements extends Component {
  constructor(props) {
    super(props);
    this.state = {
      breadcrumbItems: [
        { title: "Member Management", link: "#" },
        { title: "Manage Organizer", link: "#" },
      ],
      customchk: true,
      toggleSwitch: true,
      // page states
      name: "",
      desc: "",
      duration: "",
      price: "",
      data: [],
      alert: false,
      message: "",
      type: "",
      result: [],
      edit_enable: false,
      edit_item_id: "",
      image_select: false,
      img_url: "",
      category_type: "",
      seletedFile: null,
      category_all: [],
      category_id: "",
      modal_standard: false,
      activeModal: null,
      deleteLoading: false,
      singlebtn: false,
      seletedId: "",
      actionItems: [
        { id: "1", value: "Approve" },
        { id: "2", value: "Freeze" },
        { id: "3", value: "Edit Organizer" },
        { id: "4", value: "Block Organizer" },
      ],
      sleetedAction: "",
    };
    this.tog_standard = this.tog_standard.bind(this);
  }

  tog_standard() {
    this.setState((prevState) => ({
      modal_standard: !prevState.modal_standard,
    }));
    this.removeBodyCss();
  }
  removeBodyCss() {
    document.body.classList.add("no_padding");
  }

  componentDidMount = async () => {
    // alert();

    let resultCat = await HttpClient.requestData("admin/menu-category", "GET");

    if (resultCat && resultCat.status) {
      this.setState({ category_all: resultCat.data });
    }
    this.fetchData();
  };

  handleChange = (event) => {
    console.log(event.target.value);
    this.setState({ category_id: event.target.value });
  };

  deleteModal = (data, index) => {
    this.setState({ modal_standard: true, activeModal: data });
  };

  handleChangeAction = (value, element) => {
    console.log(value);
    // this.setState({ sleetedAction: value });
    if (value == "1") {
      this.approve(element, "approve");
    } else if (value == "3") {
      window.open(`/organizer-profile/${element._id}`);
    } else if (value == "2") {
      this.approve(element, "freez");
    } else if (value == "4") {
      this.approve(element, "block");
    }
  };

  getStatus = (element) => {
    let status = "";
    if (element?.approve) {
      status = "Approved";
      if (element?.block) {
        status = "Blocked";
      }
      if (element?.freez) {
        status = "Freezed";
      }
    } else {
      status = "Pending Approval";
    }
    return status;
  };

  actionHandeler = (element) => {
    let actionItems = [
      { id: "1", value: element?.approve ? "Not Approve" : "Approve" },
      { id: "2", value: element?.freez ? "Unfreeze" : "Freeze" },
      { id: "3", value: "Edit Organizer" },
      {
        id: "4",
        value: element?.block ? "Unblock Organizer" : "Block Organizer",
      },
    ];

    if (element?.approve) {
      actionItems.shift();
    }

    return (
      <div style={{ display: "flex", flexDirection: "row" }}>
        <select
          className="form-control"
          onChange={(event) =>
            this.handleChangeAction(event.target.value, element)
          }
          value={this.state.sleetedAction}
        >
          <option value="" disabled>
            Select Option
          </option>
          {actionItems.map((item, index) => {
            return (
              <option value={item.id} key={index}>
                {item.value}
              </option>
            );
          })}
        </select>
        {/* <Dropdown
          isOpen={this.state.singlebtn && this.state.seletedId == element._id}
          toggle={() => {
            this.setState({
              singlebtn: !this.state.singlebtn,
              seletedId: element._id,
            });
          }}
        >
          <DropdownToggle color="primary" caret>
            Dropdown button <i className="mdi mdi-chevron-down"></i>
          </DropdownToggle>
          <DropdownMenu>
            <DropdownItem>Action</DropdownItem>
            <DropdownItem>Another action</DropdownItem>
            <DropdownItem>Something else here</DropdownItem>
          </DropdownMenu>
        </Dropdown> */}
      </div>
    );
  };

  fetchData = async () => {
    let result = await HttpClient.requestData("admin/organizer", "GET");
    // console.log("result", result);
    if (result && result.status) {
      let data = [];
      let i = 1;
      this.setState({ result: result.data });
      this.state.result.forEach((element, index) => {
        let rows = {
          sl: i,
          fname: (
            <Link to={"/organizer-profile/" + element._id} onClick={() => {}}>
              {element.name}
            </Link>
          ),
          lname: (
            <>
              <div>
                <span>{element.phone} </span>
                <span
                  title={
                    element?.phoneVerified ? "Verified" : "Pending Verification"
                  }
                >
                  <i
                    class={
                      element?.phoneVerified
                        ? "fa fa-check-circle"
                        : "fa fa-times-circle"
                    }
                    aria-hidden="true"
                    style={{ color: element?.phoneVerified ? "green" : "red" }}
                  ></i>
                </span>
              </div>
              <div>
                <span>{element.email} </span>
                <span
                  title={
                    element?.emailVerified ? "Verified" : "Pending Verification"
                  }
                >
                  {/* ({element?.emailVerified ? "Verified":"Pending Verification"}) */}
                  <i
                    class={
                      element?.emailVerified
                        ? "fa fa-check-circle"
                        : "fa fa-times-circle"
                    }
                    aria-hidden="true"
                    style={{ color: element?.emailVerified ? "green" : "red" }}
                  ></i>
                </span>
              </div>
            </>
          ),
          // email: element.email,
          organizerType: element?.organizerTypeData?.name ?? "",
          paymentMethod: element?.paymentType
            ? element?.paymentType
                .filter((item) => item.status == true)
                .map((x) => x.name)
                .join()
            : "",
          numOfEvents: element?.events ? element?.events.length : "0",
          location: element?.cityData?.name + " , " + element?.stateData?.name,
          rgt_date: element.zipCode,
          status: (
            <>
              {/* <button
                title="Delete"
                className="btn btn-danger mr-2"
                onClick={() => {
                  this.delete(element, index);
                }}
              >
                <i className="fa fa-trash" />
              </button> */}
              <button
                title={this.getStatus(element)}
                className={
                  this.getStatus(element) == "Approved"
                    ? "btn btn-success"
                    : "btn btn-danger"
                }
                // onClick={() => {
                //   this.approve(element, index);
                // }}
              >
                {/* {element.approve ? "Approved" : "Pending Approval"} */}
                {this.getStatus(element)}
              </button>
            </>
          ),
          action: this.actionHandeler(element),
          // action: (
          //   <div style={{ display: "flex", flexDirection: "row" }}>
          //     <button
          //       title="Delete"
          //       className="btn btn-danger mr-2"
          //       onClick={() => {
          //         this.deleteModal(element, index);
          //       }}
          //     >
          //       <i className="fa fa-trash" />
          //     </button>
          //     <Link
          //       to={"/organizer-profile/" + element._id}
          //       title="View"
          //       className="btn btn-info mr-2"
          //       onClick={() => {

          //       }}
          //     >
          //       <i className="fa fa-eye" />
          //     </Link>

          //   </div>
          // ),
        };
        i++;
        data.push(rows);
      });
      this.setState({
        data: data,
      });
    } else {
      this.setState({
        data: [],
      });
    }
  };

  delete = async (item, index) => {
    this.setState({ deleteLoading: true });
    // return false
    let result = await HttpClient.requestData(
      "admin/organizer/" + this.state.activeModal._id,
      "DELETE"
    );
    if (result && result.status) {
      let index = this.state.result.indexOf(this.state.activeModal);
      if (index > -1) {
        this.state.result.splice(index, 1);
        this.setState({
          alert: true,
          message: "Deleted Successfully",
          type: "success",
          deleteLoading: false,
          activeModal: null,
          modal_standard: false,
        });
        setTimeout(() => {
          this.setState({
            alert: false,
            message: "",
            type: "",
          });
        }, 3000);
        this.fetchData();
      }
    } else {
    }
  };

  edit = async (item, index) => {
    console.log("item.category", item);
    this.setState({
      edit_enable: true,
      name: item.name,
      edit_item_id: item._id,
      category_id: item.categoty,
    });
  };

  approve = async (item, type) => {
    let data = {};
    let msg = "";
    if (type == "approve") {
      data = { approve: !item.approve };
      msg =
        item.approve == false
          ? "Organizer Approved Successfully."
          : "Organizer Not Approved Successfully.";
    } else if (type == "block") {
      data = { block: item?.block ? !item?.block : true };
      msg =
        item?.block == false || typeof item.block == "undefined"
          ? "Organizer Blocked Successfully."
          : "Organizer Unblocked Successfully.";
    } else if (type == "freez") {
      data = { freez: item?.freez ? !item?.freez : true };
      msg =
        item?.freez == false || typeof item.freez == "undefined"
          ? "Organizer Freez Successfully."
          : "Organizer Unfreez Successfully.";
    }
    let result = await HttpClient.requestData(
      "admin/organizer/" + item._id,
      "PUT",
      data
    );
    if (result && result.status) {
      this.setState({
        alert: true,
        message: msg,
        type: "success",
      });
      this.fetchData();
    } else {
      this.setState({
        alert: true,
        message: "Server Error !!! Try Again.",
        type: "danger",
      });
    }
    setTimeout(() => {
      this.setState({
        alert: false,
        message: "",
        type: "",
      });
    }, 3000);
  };

  render() {
    const data = {
      // omit...
      headerStyle: { backgroundColor: "green" },
      columns: [
        {
          label: "Sl.",
          field: "sl",
          sort: "asc",
          width: 150,
        },
        {
          label: "Name",
          field: "fname",
          sort: "asc",
          width: 270,
        },
        {
          label: "Phone & Email",
          field: "lname",
          width: 270,
          sort: "disabled",
        },
        // {
        //   label: "Email",
        //   field: "email",
        //   width: 270,
        //   sort: "disabled",
        // },
        {
          label: "Organizer Type",
          field: "organizerType",
          width: 270,
          sort: "disabled",
        },
        {
          label: "Payment Method",
          field: "paymentMethod",
          width: 270,
          sort: "disabled",
        },
        {
          label: "Number of Events",
          field: "numOfEvents",
          width: 270,
          sort: "disabled",
        },
        {
          label: "Location",
          field: "location",
          width: 270,
          sort: "disabled",
        },
        {
          label: "Status",
          field: "status",
          width: 200,
          sort: "disabled",
        },
        {
          label: "Actions",
          field: "action",
          width: 300,
          sort: "disabled",
        },
      ],
      rows: this.state.data,
    };
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid>
            {/* start of modal */}
            <Modal
              isOpen={this.state.modal_standard}
              toggle={this.tog_standard}
            >
              <ModalHeader
                toggle={() => this.setState({ modal_standard: false })}
              >
                Are you sure you want to delete{" "}
                {this.state.activeModal != null && this.state.activeModal.name}{" "}
                ?
              </ModalHeader>
              <ModalBody>
                <h5>
                  Organizer Name -{" "}
                  {this.state.activeModal != null &&
                    this.state.activeModal.name}
                </h5>
                <p>
                  If you want to delete click on Delete Now otherwize click on
                  close.
                </p>
              </ModalBody>
              <ModalFooter>
                <Button
                  type="button"
                  onClick={this.tog_standard}
                  color="light"
                  className="waves-effect"
                >
                  Close
                </Button>
                <Button
                  type="button"
                  color="danger"
                  className="waves-effect waves-light"
                  onClick={() => {
                    this.delete();
                  }}
                >
                  {this.state.deleteLoading ? (
                    <Spinner size="sm" color="secondary" />
                  ) : (
                    "Delete Now"
                  )}
                </Button>
              </ModalFooter>
            </Modal>
            {/* end of modal */}
            <Breadcrumbs
              title={this.state.breadcrumbItems[1].title}
              breadcrumbItems={this.state.breadcrumbItems}
            />

            <Row>
              <Col xl={12}>
                {this.state.alert ? (
                  <UncontrolledAlert
                    color={this.state.type}
                    className="alert-dismissible fade show"
                    role="alert"
                  >
                    {this.state.type == "warning" ? (
                      <i className="mdi mdi-alert-outline mr-2"></i>
                    ) : this.state.type == "success" ? (
                      <i className="mdi mdi-check-all mr-2"></i>
                    ) : this.state.type == "danger" ? (
                      <i className="mdi mdi-block-helper mr-2"></i>
                    ) : null}
                    {this.state.message}
                  </UncontrolledAlert>
                ) : null}
                <div className="card p-3">
                  <MDBDataTableV5 responsive bordered data={data} />
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    );
  }
}

export default React.memo(FormElements);
