import { MDBDataTableV5 } from "mdbreact";
import moment from "moment";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  Button,
  Col,
  Container,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Spinner,
  UncontrolledAlert,
} from "reactstrap";
//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";
import HttpClient from "./../../utils/HttpClient";

class FormElements extends Component {
  constructor(props) {
    super(props);
    this.state = {
      breadcrumbItems: [
        { title: "Events Management", link: "#" },
        { title: "Manage Events", link: "#" },
      ],
      customchk: true,
      toggleSwitch: true,
      // page states
      name: "",
      desc: "",
      duration: "",
      price: "",
      data: [],
      alert: false,
      message: "",
      type: "",
      result: [],
      edit_enable: false,
      edit_item_id: "",
      image_select: false,
      img_url: "",
      category_type: "",
      seletedFile: null,
      category_all: [],
      category_id: "",
      modal_standard: false,
      activeModal: null,
      deleteLoading: false,
      key: this.props.match.params.type,
      updated: false,
    };
    this.tog_standard = this.tog_standard.bind(this);
  }

  tog_standard() {
    this.setState((prevState) => ({
      modal_standard: !prevState.modal_standard,
    }));
    this.removeBodyCss();
  }
  removeBodyCss() {
    document.body.classList.add("no_padding");
  }

  componentDidMount = async () => {
    this.fetchData();
  };

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.match.params.type !== this.props.match.params.type) {
      window.location.reload();
    } else {
      return;
    }
  }

  handleChange = (event) => {
    this.setState({ category_id: event.target.value });
  };

  deleteModal = (data, index) => {
    this.setState({ modal_standard: true, activeModal: data });
  };

  getLiveEvents = (data, type) => {
    let filterData = [];
    if (type == 0) {
      return data;
    } else if (type == 1) {
      filterData =
        data &&
        data.filter(
          (elm) =>
            new Date(elm.startDate) < new Date() &&
            new Date(elm.endDate) > new Date()
        );
    } else if (type == 2) {
      filterData =
        data &&
        data.filter(
          (elm) =>
            new Date(elm.startDate) > new Date() &&
            new Date(elm.endDate) > new Date()
        );
    } else if (type == 3) {
      filterData =
        data && data.filter((elm) => new Date(elm.endDate) < new Date());
    } else {
      return data;
    }
    return filterData;
  };

  fetchData = async () => {
    this.setState({ updated: !this.state.updated });
    let result = await HttpClient.requestData("admin/event", "GET");
    if (result && result.status) {
      let data = [];
      let i = 1;
      let resultData = this.getLiveEvents(
        result.data,
        this.state.key == "live"
          ? "1"
          : this.state.key == "upcoming"
          ? "2"
          : this.state.key == "past"
          ? "3"
          : "0"
      );
      this.setState({
        result: resultData,
      });
      resultData.forEach((element, index) => {
        let rows = {
          sl: i,
          image: (
            <img
              src={`${process.env.REACT_APP_IMGURL}/${element.image}`}
              style={{ height: 120, width: 120 }}
            />
          ),
          name: element.name,
          date:
            moment(element.startDate).format("DD-MM-YYYY") +
            " to " +
            moment(element.endDate).format("DD-MM-YYYY"),
          time:
            moment(element.startTime).format("hh:mm A") +
            " - " +
            moment(element.endTime).format("hh:mm A"),
          organizer: element.organizerData && element.organizerData.name,
          rgt_date: moment(element.createOn).format("LLL"),
          status: (
            <>
              {/* <button
                title="Delete"
                className="btn btn-danger mr-2"
                onClick={() => {
                  this.delete(element, index);
                }}
              >
                <i className="fa fa-trash" />
              </button> */}
              <button
                title={element.approve ? "Approve" : "Not Approve"}
                className={
                  element.approve ? "btn btn-success" : "btn btn-danger"
                }
                onClick={() => {
                  this.approve(element, index);
                }}
              >
                {element.approve ? "Approve" : "Disapprove"}
              </button>
            </>
          ),
          action: (
            <div style={{ display: "flex", flexDirection: "row" }}>
              <button
                title="Delete"
                className="btn btn-danger mr-2"
                onClick={() => {
                  this.deleteModal(element, index);
                }}
              >
                <i className="fa fa-trash" />
              </button>
              <Link
                to={"/events-details/" + element._id}
                title="View"
                className="btn btn-info mr-2"
                onClick={() => {
                  // this.delete(element, index);
                }}
              >
                <i className="fa fa-eye" />
              </Link>
            </div>
          ),
        };
        i++;
        data.push(rows);
      });
      this.setState({
        data: data,
        updated: !this.state.updated,
      });
    } else {
      this.setState({
        data: [],
        updated: !this.state.updated,
      });
    }
  };

  delete = async (item, index) => {
    this.setState({ deleteLoading: true });
    // return false
    let result = await HttpClient.requestData(
      "admin/event/" + this.state.activeModal._id,
      "DELETE"
    );
    if (result && result.success) {
      let index = this.state.result.indexOf(this.state.activeModal);
      if (index > -1) {
        this.state.result.splice(index, 1);
        this.setState({
          alert: true,
          message: "Deleted Successfully",
          type: "success",
          deleteLoading: false,
          activeModal: null,
          modal_standard: false,
        });
        setTimeout(() => {
          this.setState({
            alert: false,
            message: "",
            type: "",
          });
        }, 3000);
        this.fetchData();
      }
    } else {
    }
  };

  edit = async (item, index) => {
    console.log("item.category", item);
    this.setState({
      edit_enable: true,
      name: item.name,
      edit_item_id: item._id,
      category_id: item.categoty,
    });
  };

  approve = async (item, index) => {
    let result = await HttpClient.requestData(
      "admin/event/" + item._id,
      "PUT",
      { approve: !item.approve }
    );
    if (result && result.status) {
      this.setState({
        alert: true,
        message:
          item.approve == false
            ? "Approved Successfully."
            : "Not Approved Successfully.",
        type: "success",
      });
      this.fetchData();
    } else {
      this.setState({
        alert: true,
        message: "Server Error !!! Try Again.",
        type: "danger",
      });
    }

    setTimeout(() => {
      this.setState({
        alert: false,
        message: "",
        type: "",
      });
    }, 3000);
  };

  render() {
    const data = {
      columns: [
        {
          label: "Sl.",
          field: "sl",
          sort: "asc",
          width: 150,
        },
        {
          label: "Image",
          field: "image",
          sort: "disabled",
          width: 270,
        },
        {
          label: "Name",
          field: "name",
          sort: "asc",
          width: 270,
        },
        {
          label: "Date",
          field: "date",
          sort: "disabled",
          width: 270,
        },
        {
          label: "Time",
          field: "time",
          sort: "disabled",
          width: 270,
        },
        {
          label: "Organizer",
          field: "organizer",
          sort: "disabled",
          width: 270,
        },
        {
          label: "Created At",
          field: "rgt_date",
          sort: "asc",
          width: 270,
        },
        {
          label: "Status",
          field: "status",
          sort: "disabled",
          width: 100,
        },
        {
          label: "Actions",
          field: "action",
          sort: "disabled",
          width: 100,
        },
      ],
      rows: this.state.data,
    };
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid>
            {/* start of modal */}
            <Modal
              isOpen={this.state.modal_standard}
              toggle={this.tog_standard}
            >
              <ModalHeader
                toggle={() => this.setState({ modal_standard: false })}
              >
                Are you sure you want to delete{" "}
                {this.state.activeModal != null && this.state.activeModal.name}{" "}
                ?
              </ModalHeader>
              <ModalBody>
                <h5>
                  Event Name -{" "}
                  {this.state.activeModal != null &&
                    this.state.activeModal.name}
                </h5>
                <p>
                  If you want to delete click on Delete Now otherwize click on
                  close.
                </p>
              </ModalBody>
              <ModalFooter>
                <Button
                  type="button"
                  onClick={this.tog_standard}
                  color="light"
                  className="waves-effect"
                >
                  Close
                </Button>
                <Button
                  type="button"
                  color="danger"
                  className="waves-effect waves-light"
                  onClick={() => {
                    this.delete();
                  }}
                >
                  {this.state.deleteLoading ? (
                    <Spinner size="sm" color="secondary" />
                  ) : (
                    "Delete Now"
                  )}
                </Button>
              </ModalFooter>
            </Modal>
            {/* end of modal */}
            <Breadcrumbs
              title={this.state.breadcrumbItems[1].title}
              breadcrumbItems={this.state.breadcrumbItems}
            />

            <Row>
              <Col xl={12}>
                {this.state.alert ? (
                  <UncontrolledAlert
                    color={this.state.type}
                    className="alert-dismissible fade show"
                    role="alert"
                  >
                    {this.state.type == "warning" ? (
                      <i className="mdi mdi-alert-outline mr-2"></i>
                    ) : this.state.type == "success" ? (
                      <i className="mdi mdi-check-all mr-2"></i>
                    ) : this.state.type == "danger" ? (
                      <i className="mdi mdi-block-helper mr-2"></i>
                    ) : null}
                    {this.state.message}
                  </UncontrolledAlert>
                ) : null}
                <div className="card p-3">
                  <MDBDataTableV5
                    responsive
                    bordered
                    data={{ rows: this.state.data, columns: data.columns }}
                  />
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    );
  }
}

export default React.memo(FormElements);
