import React, { Component } from "react";
import {
  Button,
  Card,
  CardBody,
  CardImg,
  CardTitle,
  Col,
  Container,
  FormGroup,
  Input,
  Label,
  Row,
  UncontrolledAlert,
} from "reactstrap";

import { MDBDataTable } from "mdbreact";
import { reactLocalStorage } from "reactjs-localstorage";
import MiniWidgets from "../Dashboard/MiniWidgets";
import HttpClient from "./../../utils/HttpClient";
import { Link } from "react-router-dom";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

class Pricing extends Component {
  constructor(props) {
    super(props);
    this.state = {
      breadcrumbItems: [
        { title: "Organizers", link: "/organizer" },
        { title: "Organizers Profile", link: "#" },
      ],
      activeTab: "1",
      plans: [],
      activePlan: [],
      userId: "",
      userDetails: {},
      alert: false,
      message: "",
      type: "",

      name_old: "",
      email_old: "",
      mobile_old: "",

      image: "",
      name: "",
      email: "",
      mobile: "",
      address: "",
      edit_enable: true,
      _id: "",
      new_pass: "",
      con_new_pass: "",
      website: "",
      approve: false,
      organizerInformation: "",
      ownerInfo: [],
      data: [],
      edit_id: this.props.match.params.id,
      reports: [
        {
          icon: "ri-stack-line",
          title: "Number of events",
          value: "0",
          rate: "2.4%",
          desc: "From previous period",
        },
        {
          icon: "ri-stack-line",
          title: "Total revenue from all events",
          value: "0",
          rate: "2.4%",
          desc: "From previous period",
        },
        {
          icon: "ri-stack-line",
          title: "Number of tickets sold",
          value: "0",
          rate: "2.4%",
          desc: "From previous period",
        },
        {
          icon: "ri-store-2-line",
          title: "Total revenue on tickets",
          value: "$ 0",
          rate: "2.4%",
          desc: "From previous period",
        },
        {
          icon: "ri-stack-line",
          title: "Number of reservations",
          value: "$ 0",
          rate: "2.4%",
          desc: "From previous period",
        },
        {
          icon: "ri-briefcase-4-line",
          title: "Total revenue on reservations",
          value: "$ 0",
          rate: "2.4%",
          desc: "From previous period",
        },
      ],
      bookings: [],
      paidUsers: [],
    };
    this.toggleTab = this.toggleTab.bind(this);
  }

  toggleTab(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
    }
  }

  componentDidMount = () => {
    this.fetchUser();
  };

  getValue = (data, type, valueType) => {
    let filterArr = data.filter((ele) => ele.bookingType === type);
    if (valueType === "0") {
      let totalPrice = data.reduce((n, { total }) => n + total, 0);
      return Number(totalPrice).toFixed(1);
    } else if (valueType === "1") {
      let totalTickets = 0;
      filterArr.map((ele) => {
        let tickets = ele?.tickets.reduce((n, { count }) => n + count, 0);
        totalTickets = Number(totalTickets) + Number(tickets);
      });
      return type == "ticket" ? totalTickets : filterArr.length;
    } else {
      let totalPrice = filterArr.reduce((n, { total }) => n + total, 0);
      return Number(totalPrice).toFixed(1);
    }
  };

  fetchUser = async () => {
    let sendData = { id: this.state.userId };
    let result = await HttpClient.requestData(
      "admin/organizer/" + this.state.edit_id,
      "GET"
    );
    if (result && result.status) {
      let bookings = result.data?.bookings ?? [];

      let paidUsers = [];
      let j = 1;
      [...new Map(bookings.map((item) => [item["userId"], item])).values()].map(
        (element, index) => {
          let rows = {
            sl: j,
            name:
              element?.userData?.firstname + " " + element?.userData?.lastname,
            phone: element?.userData?.phone,
            email: element?.userData?.email,
            action: (
              <div style={{ display: "flex", flexDirection: "row" }}>
                <Link
                  to={"/user-profile/" + element?.userData?._id}
                  title="View"
                  className="btn btn-info mr-2"
                  onClick={() => {
                    // this.delete(element, index);
                  }}
                  target={"_blank"}
                >
                  <i className="fa fa-eye" />
                </Link>
              </div>
            ),
          };
          j++;
          paidUsers.push(rows);
        }
      );
      this.setState({
        paidUsers: paidUsers,
      });

      this.state.reports[0].value = result.data?.events?.length ?? 0;
      this.state.reports[1].value = `$ ${this.getValue(bookings, "all", "0")}`;

      this.state.reports[2].value = this.getValue(bookings, "ticket", "1");
      this.state.reports[3].value = `$ ${this.getValue(
        bookings,
        "ticket",
        "2"
      )}`;
      this.state.reports[4].value = this.getValue(bookings, "reservation", "1");
      this.state.reports[5].value = `$ ${this.getValue(
        bookings,
        "reservation",
        "2"
      )}`;

      this.setState({
        bookings: bookings,
        image:
          result.data.image ==
          "https://eshendetesia.com/images/user-profile.png"
            ? result.data.image
            : "http://35.183.124.16:8000/" + result.data.image,
        name: result.data.name,
        email: result.data.email,
        mobile: result.data.phone,
        //   _id: result.data._id,

        name_old: result.data.name,
        email_old: result.data.email,
        mobile_old: result.data.phone,
        address: result.data.address ? result.data.address : "",
        website: result.data.website,

        approve: result.data.approve,
        organizerInformation: result.data.organizerInformation,
        ownerInfo:
          typeof result.data.ownerInfo == "undefined"
            ? []
            : result.data.ownerInfo,
      });

      let data = [];
      let i = 1;
      this.state.ownerInfo.map((element, index) => {
        let rows = {
          sl: i,
          name: element.fanme + " " + element.lname,
          phone: element.phoneNumber,
          email: element.emailId,
        };
        i++;
        data.push(rows);
      });
      this.setState({
        data: data,
      });
    } else {
    }
  };

  formValid = async (item) => {
    let sendData = {
      owner_id: Number(this.state.userId),
      sub_id: Number(item.id),
    };
    let result = await HttpClient.requestData(
      "app-owner/subscription/buy-now",
      "POST",
      sendData
    );
    if (result && result.status > 0) {
      this.setState({
        alert: true,
        message: "Plan Subscribed Successfully",
        type: "success",
      });
      this.fetchActivePlans();
      this.fetchPlans();
    } else {
      this.setState({ alert: true, message: result.error, type: "danger" });
    }
    setTimeout(() => {
      this.setState({
        alert: false,
        message: "",
        type: "",
      });
    }, 3000);
  };

  imageUpload = async (e) => {
    // console.log("e", e.target.files);
    let file = e.target.files;
    //   if(file.lenght>0)
    //   {
    let dataSend = { buckate: true, buckate_name: "appIcon" };

    let result = await HttpClient.newFileUpload(
      "image-upload/profile",
      e.target.files[0],
      dataSend
    );
    console.log("result", result);
    if (result && result.status) {
      this.setState({ image: result.url, image_select: true });
    }

    //   }
  };

  submit = async () => {
    if (
      this.state.name != "" &&
      this.state.email != "" &&
      this.state.mobile != "" &&
      this.state.image != ""
    ) {
      let data = null;
      let result = null;
      // return false;
      data = {
        name: this.state.name,
        email: this.state.email,
        mobile: this.state.mobile,
        address: this.state.address,
        website: this.state.website,
        organizerInformation: this.state.organizerInformation,
      };
      console.log("data", data);
      result = await HttpClient.requestData(
        "admin/organizer/" + this.state.edit_id,
        "PUT",
        data
      );

      if (result && result.status) {
        this.setState({
          alert: true,
          message: this.state.edit_enable
            ? "Profile Updated Successfully"
            : "Profile Added Successfully",
          type: "success",
        });

        this.fetchUser();
      } else {
        this.setState({
          alert: true,
          message:
            typeof result.error == "string"
              ? result.error
              : result.error[0].msg,
          type: "danger",
        });
      }
    } else {
      this.setState({
        alert: true,
        message: "Please Fill Up All Details.",
        type: "warning",
      });
    }
    setTimeout(() => {
      this.setState({
        alert: false,
        message: "",
        type: "",
      });
    }, 3000);
  };

  submitPass = async () => {
    if (this.state.new_pass != "" && this.state.con_new_pass != "") {
      if (this.state.new_pass == this.state.con_new_pass) {
        let data = null;
        let result = null;
        // return false;
        data = {
          password: this.state.new_pass,
          id: this.state._id,
        };
        console.log("data", data);
        result = await HttpClient.requestData(
          "admin/update-pass",
          "POST",
          data
        );

        if (result && result.status) {
          reactLocalStorage.setObject("userData", result.data);
          this.setState({
            alert: true,
            message: this.state.edit_enable
              ? "Password Updated Successfully"
              : "Password Added Successfully",
            type: "success",
          });

          this.fetchUser();
        } else {
          this.setState({
            alert: true,
            message:
              typeof result.error == "string"
                ? result.error
                : result.error[0].msg,
            type: "danger",
          });
        }
      } else {
        this.setState({
          alert: true,
          message: "Password Not Matched.",
          type: "warning",
        });
      }
    } else {
      this.setState({
        alert: true,
        message: "Please Enter Password And Confirm Password",
        type: "warning",
      });
    }
    setTimeout(() => {
      this.setState({
        alert: false,
        message: "",
        type: "",
      });
    }, 3000);
  };

  render() {
    const data = {
      columns: [
        {
          label: "Sl.",
          field: "sl",
          sort: "asc",
          width: 150,
        },
        {
          label: "Name",
          field: "name",
          sort: "asc",
          width: 270,
        },

        {
          label: "Phone",
          field: "phone",
          sort: "asc",
          width: 270,
        },
        {
          label: "Email",
          field: "email",
          sort: "asc",
          width: 270,
        },
      ],
      rows: this.state.data,
    };

    const paidUsers = {
      columns: [
        {
          label: "Sl.",
          field: "sl",
          sort: "asc",
          width: 150,
        },
        {
          label: "Name",
          field: "name",
          sort: "asc",
          width: 270,
        },

        {
          label: "Phone",
          field: "phone",
          sort: "asc",
          width: 270,
        },
        {
          label: "Email",
          field: "email",
          sort: "asc",
          width: 270,
        },
        {
          label: "Action",
          field: "action",
          sort: "asc",
          width: 270,
        },
      ],
      rows: this.state.paidUsers,
    };

    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid>
            <Breadcrumbs
              title={this.state.breadcrumbItems[1].title}
              breadcrumbItems={this.state.breadcrumbItems}
            />
            <Row>
              <Col xl={12}>
                <Row>
                  <MiniWidgets reports={this.state.reports} md={4} />
                </Row>

                {/* revenue Analytics */}
              </Col>
            </Row>
            <Row>
              <Col lg={12}>
                {this.state.alert ? (
                  <UncontrolledAlert
                    color={this.state.type}
                    className="alert-dismissible fade show"
                    role="alert"
                  >
                    {this.state.type == "warning" ? (
                      <i className="mdi mdi-alert-outline mr-2"></i>
                    ) : this.state.type == "success" ? (
                      <i className="mdi mdi-check-all mr-2"></i>
                    ) : this.state.type == "danger" ? (
                      <i className="mdi mdi-block-helper mr-2"></i>
                    ) : null}
                    {this.state.message}
                  </UncontrolledAlert>
                ) : null}
              </Col>
            </Row>
            <Row>
              <Col xs={4} className="mb-6">
                <Card>
                  <CardImg
                    top
                    className="img-fluid"
                    src={this.state.image}
                    alt="Skote"
                    style={{
                      alignSelf: "center",
                      width: 200,
                      height: 200,
                      borderRadius: 100,
                      marginTop: 20,
                    }}
                  />
                  <CardBody>
                    <CardTitle className="mt-0">Profile Details</CardTitle>
                    <div>
                      <small className="text-muted">Name: </small>
                      <p>{this.state.name_old}</p>
                      <hr />
                      <small className="text-muted">Mobile Number: </small>
                      <p>{this.state.mobile_old}</p>
                      <hr />{" "}
                      <small className="text-muted">Email address: </small>
                      <p>{this.state.email_old}</p>
                      <hr />
                      <small className="text-muted">Status: </small>
                      <p
                        className={
                          this.state.approve ? "text-success" : "text-danger"
                        }
                      >
                        {this.state.approve ? "Approved" : "Disapproved"}
                      </p>
                      <hr />
                      <small className="text-muted">
                        Total Number Of Paid Users:{" "}
                      </small>
                      <p>
                        {
                          [
                            ...new Set(
                              this.state.bookings.map((item) => item.userId)
                            ),
                          ].length
                        }
                      </p>
                    </div>
                  </CardBody>
                </Card>
              </Col>
              <Col xs={8} className="mb-6">
                <Card>
                  <CardBody>
                    <h4 className="card-title">Edit Profile</h4>
                    <FormGroup row>
                      <Label
                        htmlFor="example-text-input"
                        className="col-md-2 col-form-label"
                      >
                        Name
                      </Label>
                      <Col md={10}>
                        <Input
                          className="form-control"
                          type="text"
                          id="example-text-input"
                          value={this.state.name}
                          onChange={(val) => {
                            this.setState({ name: val.target.value });
                          }}
                          placeholder={"Enter name"}
                        />
                      </Col>
                    </FormGroup>
                    <FormGroup row>
                      <Label
                        htmlFor="example-text-input"
                        className="col-md-2 col-form-label"
                      >
                        Email
                      </Label>
                      <Col md={10}>
                        <Input
                          className="form-control"
                          type="text"
                          id="example-text-input"
                          value={this.state.email}
                          onChange={(val) => {
                            this.setState({ email: val.target.value });
                          }}
                          placeholder={"Enter email"}
                        />
                      </Col>
                    </FormGroup>{" "}
                    <FormGroup row>
                      <Label
                        htmlFor="example-text-input"
                        className="col-md-2 col-form-label"
                      >
                        Mobile
                      </Label>
                      <Col md={10}>
                        <Input
                          className="form-control"
                          type="text"
                          id="example-text-input"
                          value={this.state.mobile}
                          onChange={(val) => {
                            this.setState({ mobile: val.target.value });
                          }}
                          placeholder={"Enter mobile"}
                        />
                      </Col>
                    </FormGroup>{" "}
                    <FormGroup row>
                      <Label
                        htmlFor="example-text-input"
                        className="col-md-2 col-form-label"
                      >
                        Website
                      </Label>
                      <Col md={10}>
                        <Input
                          className="form-control"
                          type="text"
                          id="example-text-input"
                          value={this.state.website}
                          onChange={(val) => {
                            this.setState({ website: val.target.value });
                          }}
                          placeholder={"Enter Website"}
                        />
                      </Col>
                    </FormGroup>{" "}
                    <FormGroup row>
                      <Label
                        htmlFor="example-search-input"
                        className="col-md-2 col-form-label"
                      >
                        Address
                      </Label>
                      <Col md={10}>
                        <textarea
                          id="question-input1"
                          className="form-control"
                          rows="2"
                          placeholder="Enter your adress here..."
                          onChange={(val) => {
                            this.setState({ address: val.target.value });
                          }}
                          value={this.state.address}
                        ></textarea>
                      </Col>
                    </FormGroup>{" "}
                    <FormGroup row>
                      <Label
                        htmlFor="example-search-input"
                        className="col-md-2 col-form-label"
                      >
                        Organizer Information
                      </Label>
                      <Col md={10}>
                        <textarea
                          id="question-input1"
                          className="form-control"
                          rows="2"
                          placeholder="Enter your organizer information here..."
                          onChange={(val) => {
                            this.setState({
                              organizerInformation: val.target.value,
                            });
                          }}
                          value={this.state.organizerInformation}
                        ></textarea>
                      </Col>
                    </FormGroup>{" "}
                    <FormGroup className="mb-0">
                      <div className="button-items pt-4">
                        <Button
                          color="primary"
                          type="button"
                          className="waves-effect waves-light mr-1"
                          onClick={() => {
                            this.submit();
                          }}
                        >
                          {this.state.edit_enable ? "Update" : "Update"}{" "}
                          <i className="ri-arrow-right-line align-middle ml-1"></i>
                        </Button>
                      </div>
                    </FormGroup>
                  </CardBody>
                </Card>
                <Card>
                  <CardBody>
                    <h4 className="card-title">Owner Info</h4>
                    <div className="card p-3">
                      <MDBDataTable responsive bordered data={data} />
                    </div>
                  </CardBody>{" "}
                </Card>
              </Col>
            </Row>
            <Row>
              <Col md={12}>
                <Card>
                  <CardBody>
                    <h4 className="card-title">Paid Users</h4>
                    <div className="card p-3">
                      <MDBDataTable responsive bordered data={paidUsers} />
                    </div>
                  </CardBody>{" "}
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    );
  }
}

export default Pricing;
